import React from 'react';
import './App.css'

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      value: '',
      expression: ''
    }
  }

  handleClick = (e) => {
    this.setState({
      value: this.state.value + e.target.innerText,
    })
    if (e.target.innerText === 'del' || e.target.innerText === 'DEL') {
      this.setState({
        value: this.state.value.slice(0, this.state.value.length - 1),
        expression: ''
      })
    }
    if (e.target.innerText === 'reset' || e.target.innerText === 'RESET') {
      this.setState({
        value: '',
        expression: ''
      })
    }
    if (this.state.value === '0') {
      this.setState({
        value: e.target.innerText
      })
    }
  }

  enterPeriod = () => {
    if (!(/\./).test(this.state.value)) {
            this.setState({
                value: this.state.value + '.'
            })
    }
    if (this.state.value === '') {
      this.setState({
        value: this.state.value + '0.'
      })
    }
  }
  
  equals = (e) => {
    if (e.target.innerText === '=' && this.state.value !== '' && this.state.expression !== '0') {
      this.setState({
         expression: (this.state.value - this.state.value).toString(),
        value: eval(this.state.expression + this.state.value).toString()
      })
    }else if(this.state.expression === '0'){
      this.setState({
        expression: this.state.expression
      })
    } else {
      this.setState({
        value: ''
      })
    }
  }

    enterOperator = (e) => {
        if (this.state.value.charAt(this.state.value.length - 1) === '*' ||
            this.state.value.charAt(this.state.value.length - 1) === '-' ||
            this.state.value.charAt(this.state.value.length - 1) === '/' ||
            this.state.value.charAt(this.state.value.length - 1) === '+' ||
          this.state.value.charAt(this.state.value.length - 1) === '.') {
            this.setState({
              value: this.state.value + '',
            })
        } else{
            this.setState({
              value: '',
              expression: this.state.value + e.target.innerText
            })
        }
      if (this.state.value === '') {
        this.setState({
        value: this.state.value + ''
        })
      }
    }

  multiplication = () => {
    if ( this.state.value.charAt(this.state.value.length - 1) === '*' ||
         this.state.value.charAt(this.state.value.length - 1) === '-' ||
         this.state.value.charAt(this.state.value.length - 1) === '/' ||
         this.state.value.charAt(this.state.value.length - 1) === '+' ||
         this.state.value.charAt(this.state.value.length - 1) === '.') {
      this.setState({
        value: this.state.value + ''
      })
    } else {
      this.setState({
        value: '',
        expression: this.state.value + '*'

      })
    }
  }
  
  inputZero = () => {
    if (this.state.value === '' ||
      this.state.value.charAt(this.state.value.length - 1) === '*' ||
      this.state.value.charAt(this.state.value.length - 1) === '-' ||
      this.state.value.charAt(this.state.value.length - 1) === '/' ||
      this.state.value.charAt(this.state.value.length - 1) === '+') {
      this.setState({
        value: this.state.value + ''
      })
    } else {
      this.setState({
        value: this.state.value + '0'
      })
        }
  }
  
  render() {
  
    const toggleThemeThree = () => {
      document.querySelector('main').style.background = 'hsl(268, 75%, 9%)';
      document.querySelector('.display').style.background = 'hsl(268, 71%, 12%)';
      document.querySelector('.display').style.color = 'hsl(52, 100%, 62%)';
      document.querySelector('.pill').style.background = 'hsl(268, 71%, 12%)';
      document.querySelector('.container').style.background = 'hsl(268, 71%, 12%)';
      document.querySelector('.calc').style.color = 'hsl(52, 100%, 62%)';
      document.querySelector('.expression').style.color = 'hsl(177, 92%, 70%)';
      document.documentElement.style.setProperty('--color', 'hsl(52, 100%, 62%)');
      document.documentElement.style.setProperty('--toggle', 'hsl(177, 92%, 70%)');
      document.documentElement.style.setProperty('--move', 'translate(21px, 9.5px)');

       var buttons = document.querySelectorAll('button');
      var i;
      for (i = 0; i < buttons.length; i++) {
        buttons[i].style.color = 'hsl(52, 100%, 62%)';
        buttons[i].style.background = 'hsl(268, 47%, 21%)';
        buttons[i].style.boxShadow = '0 5px 0 0 hsl(290, 70%, 36%)';
      }
      
      let theme = document.querySelectorAll('.theme-toggles h1');
      var j;
      for (j = 0; j < theme.length; j++) {
        theme[j].style.color = 'hsl(52, 100%, 62%)'
      }

      document.querySelector('.equals-btn').style.background = 'hsl(176, 100%, 44%)';
      document.querySelector('.equals-btn').style.boxShadow = '0 5px 0 0 hsl(177, 92%, 70%)';
      document.querySelector('.equals-btn').style.color = 'hsl(198, 20%, 13%)';
      document.querySelector('.del-btn').style.background = 'hsl(281, 89%, 26%)';
      document.querySelector('.del-btn').style.boxShadow = 'hsl(285, 91%, 52%)';
      document.querySelector('.del-btn').style.color = '#fff';
      document.querySelector('.reset-btn').style.background = 'hsl(281, 89%, 26%';
      document.querySelector('.reset-btn').style.boxShadow = 'hsl(285, 91%, 52%)';
      document.querySelector('.reset-btn').style.color = '#fff';
    }

    const toggleThemeTwo = () => {
      document.querySelector('.display').classList.add('themeTwo');
      document.querySelector('main').classList.add('themeTwo');
      document.querySelector('.pill').classList.add('themeTwo');
      document.querySelector('.container').classList.add('themeTwo');
      document.querySelector('.del-btn').classList.add('themeTwo');
      document.querySelector('.reset-btn').classList.add('themeTwo');
      document.querySelector('.calc').classList.add('themeTwo');
      document.querySelector('.equals-btn').classList.add('themeTwo');
      document.querySelector('.equals-btn').classList.add('themeTwo');
      document.documentElement.style.setProperty('--color', 'hsl(60, 10%, 19%)');
      document.documentElement.style.setProperty('--toggle', 'hsl(25, 98%, 40%)');
      document.documentElement.style.setProperty('--move', 'translate(-2px, 9.5px)');

      var buttons = document.querySelectorAll('button');
      var i;
      for (i = 0; i < buttons.length; i++) {
        buttons[i].classList.add('themeTwo');
        buttons[i].style.color = '';
        buttons[i].style.background = '';
        buttons[i].style.boxShadow = '';
      }
      
      let theme = document.querySelectorAll('.theme-toggles h1');
      var j;
      for (j = 0; j < theme.length; j++) {
        theme[j].classList.add('themeTwo')
        theme[j].style.color = ''

      }

      document.querySelector('main').style.background = '';
      document.querySelector('.display').style.background = '';
      document.querySelector('.display').style.color = '';
      document.querySelector('.pill').style.background = '';
      document.querySelector('.container').style.background = '';
      document.querySelector('.calc').style.color = '';
      document.querySelector('.expression').style.color = '';
    }

  const defaultTheme = () => {
    document.querySelector('.display').classList.remove('themeTwo');
    document.querySelector('main').classList.remove('themeTwo');
    document.querySelector('.pill').classList.remove('themeTwo');
    document.querySelector('.container').classList.remove('themeTwo');
    document.querySelector('.del-btn').classList.remove('themeTwo');
    document.querySelector('.reset-btn').classList.remove('themeTwo');
    document.querySelector('.calc').classList.remove('themeTwo');
    document.documentElement.style.setProperty('--color', '#fff');
    document.documentElement.style.setProperty('--toggle', 'hsl(6, 63%, 50%)');
    document.documentElement.style.setProperty('--move', 'translate(-21px, 9.5px)');
       
    let buttons = document.querySelectorAll('button');
    var i;
    for (i = 0; i < buttons.length; i++) {
      buttons[i].classList.remove('themeTwo');
        buttons[i].style.color = '';
        buttons[i].style.background = '';
        buttons[i].style.boxShadow = '';
    }
       
    let theme = document.querySelectorAll('.theme-toggles h1');
    var j;
    for (j = 0; j < theme.length; j++) {
      theme[j].classList.remove('themeTwo')
      theme[j].style.color = ''

     }

      document.querySelector('main').style.background = '';
      document.querySelector('.display').style.background = '';
      document.querySelector('.display').style.color = '';
      document.querySelector('.pill').style.background = '';
      document.querySelector('.container').style.background = '';
      document.querySelector('.calc').style.color = '';
      document.querySelector('.expression').color = '';
      document.documentElement.style.setProperty('--color', '');
      document.documentElement.style.setProperty('--toggle', '');
      document.documentElement.style.setProperty('--move', '');

      document.querySelector('.equals-btn').style.background = '';
      document.querySelector('.equals-btn').style.boxShadow = '';
      document.querySelector('.equals-btn').style.color = '';
  }
    
    return (
        <main>
          <div className='header'>
            <h1 className='calc'>calc</h1>
          <div className='theme'>
              <div className='pill'></div>
              <div class='theme-toggles'>
                <h1 onClick={defaultTheme}>1</h1>
                <h1 onClick={toggleThemeTwo}>2</h1>
                <h1 onClick={toggleThemeThree}>3</h1>
              </div>
            </div>
          </div>
        <div>
          <p className='expression'>{this.state.expression}</p>
          <input className='display' value={this.state.value} disabled />
          </div>
          <div className='container'>
            <button onClick={this.handleClick} > 7 </button>
            <button onClick={this.handleClick} > 8 </button>
            <button onClick={this.handleClick} > 9 </button>
            <button className='del-btn' onClick={this.handleClick} > del </button>
            <button onClick={this.handleClick} > 4 </button>
            <button onClick={this.handleClick} > 5 </button>
            <button onClick={this.handleClick} > 6 </button>
            <button onClick={this.enterOperator} > + </button>
            <button onClick={this.handleClick} > 1 </button>
            <button onClick={this.handleClick} > 2 </button>
            <button onClick={this.handleClick} > 3 </button>
            <button onClick={this.enterOperator} > - </button>
            <button onClick={this.enterPeriod} > . </button>
            <button onClick={this.inputZero} > 0 </button>
            <button onClick={this.enterOperator} > /</button>
            <button style={{ fontSize: 18 }} onClick={this.multiplication} > x </button>
            <button className='reset-btn' onClick={this.handleClick} > reset </button>
            <button className='equals-btn' onClick={ this.equals} > = </button>
          </div> 
        </main>
        )
    }
}

export default App